<template>
    <div id="app">
        <el-container>
            <el-header>
                <top></top>
            </el-header>
            <el-container>
                <el-aside width="200px">
                    <left></left>
                </el-aside>
                <el-main>
                    <router-view />
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
import top from "@/components/top";
import left from "@/components/left";
export default {
    name: "App",
    components: {
        top,
        left
    }
};
</script>

<style lang="stylus">
body {
    margin: 0;
    background: #eeeeee;
}
.el-header {
    padding: 0;
}
.el-main .el-main {
    background: white;
}
</style>



