<template>
    <div>
        <el-menu
            default-active="1"
            @open="handleOpen"
            @close="handleClose"
            @select="handleSelect"
            background-color="#eeeeee"
            text-color="#000000"
            :collapse="isCollapse"
        >
            <el-submenu index="1">
                <template slot="title">
                    <i class="el-icon-user-solid"></i>
                    <span>人员管理</span>
                </template>
                <el-menu-item index="1-1">基本信息</el-menu-item>
                <el-menu-item index="1-2">合同管理</el-menu-item>
                <el-menu-item index="1-3">薪资管理</el-menu-item>
            </el-submenu>
            <el-menu-item index="2">
                <i class="el-icon-info"></i>
                <span slot="title">日志管理</span>
            </el-menu-item>
            <el-menu-item index="3">
                <i class="el-icon-document"></i>
                <span slot="title">权限管理</span>
            </el-menu-item>
        </el-menu>
    </div>
</template>

<script>
export default {
    data() {
        return {
            isCollapse: false
        };
    },
    methods: {
        handleOpen(key, keyPath) {
            console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
            console.log(key, keyPath);
        },
        handleSelect(key, keyPath) {
            console.log(key, keyPath);
        }
    }
};
</script>


